.root {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    padding: 0 20px;
    height: 60px;
    width: 100%;
    background-color: rgba(20, 20, 20, 0.95);
}

.link {
    text-decoration: none;
    color: white;
}

.link:hover {
    color: rgb(166, 94, 0);
}

.link:first-child {
    margin-right: auto;
}
.link:not(:first-child) {
    margin-left: 20px;
}